import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue";
import uploadImages from "../../components/common/upload-images.vue";
import uniPopup  from "../../components/uni-popup/uni-popup.vue";
export default {
	components:{
		uniNavBar,
		uploadImages,
		uniPopup
	},
	data() {
		return {
			flag:false,
			articleStatus:"所有人可见",
			itemList:['所有人可见','仅自己可见'],
			nuiTextarea:'',
			imgList:[],
			showPopup:true,
		}
	},
	onBackPress() {
		//如果文本框有值
		if(!this.nuiTextarea && this.imgList.length<1){ return; }
		if(!this.flag){
			this.saveInner();
			return true;
		}
	},
	methods: {
		//返回
		back(){
			uni.navigateBack({
				delta:1,
			})
		},
		//发布
		release(){
			
		},
		//修改发布文章状态
		postStatus(){
			uni.showActionSheet({
				itemList:this.itemList,
				success:(res)=> {
					this.articleStatus = this.itemList[res.tapIndex];
				}
			})
		},
		//保存为草稿
		saveInner(){
			uni.showModal({
				content:'是否保存为草稿',
				cancelText:'不保存',
				confirmText:'保存',
				success: (res) => {
					if(res.confirm){
						//保存到草稿
					}else{
						//不保存
					}
					this.flag = true;
					uni.navigateBack({
						delta:1
					});
				}
			})
		},
		upload(arr){
			this.imgList=arr;
		},
		hidePopup(){
			this.showPopup = false;
		}
	}
}
